import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { EngineSystem } from '@placeos/ts-client';

import { ApplicationService } from '../../services/app.service';
import { BaseComponent } from '../../shared/base.component';
import { distinctUntilChanged, filter, first, map, take, tap, withLatestFrom } from 'rxjs/operators';
import { BehaviorSubject, combineLatest, Observable } from 'rxjs';

@Component({
    selector: 'app-bootstrap',
    templateUrl: './bootstrap.component.html',
    styleUrls: ['./bootstrap.component.scss']
})
export class BootstrapComponent extends BaseComponent implements OnInit {
    /** Whether input field is focused */
    public input_focus: boolean;
    private routeParams = new BehaviorSubject<ParamMap | undefined>(undefined);
    public currentSystemId = new BehaviorSubject<string | undefined>(undefined);
    public useManualInput = new BehaviorSubject<boolean>(false);
    public systemsList = new BehaviorSubject<EngineSystem[]>([]);
    public isLoading = new BehaviorSubject<boolean>(false);
    public currentSystem = combineLatest([
        this.systemsList,
        this.currentSystemId
    ]).pipe(map(([systems, id]) => systems.find(s => s.id === id)));

    constructor(private service: ApplicationService, private route: ActivatedRoute) {
        super();
    }

    private log = (name: string, obj: Observable<any>) => {
        this.subscription(name, obj.pipe(
            tap(t => console.log(name.toUpperCase(), t))
        ).subscribe());
    };

    public ngOnInit(): void {
        // do load manually
        this.service.Systems.doLoad();

        this.log('manual', this.useManualInput);
        this.log('routeParams', this.routeParams);
        this.log('currentSystemId', this.currentSystemId);
        this.log('currentSystem', this.currentSystem);
        this.log('systemsList', this.systemsList);
        this.log('isLoading', this.isLoading);
        this.isLoading.next(true);
        this.subscription('route.query', this.route.queryParamMap.subscribe((params) => {
            this.routeParams.next(params);
        }));

        this.subscription('route.params.currentSystem', this.routeParams
            .pipe(filter(params => params.has('system_id') || params.has('sys_id')),
                map(params => params.get('system_id') || params.get('sys_id')))
            .subscribe(next => {
                this.currentSystemId.next(next);
                this.useManualInput.next(true);
                this.configure(next);
            }));

        this.subscription('route.params.clear', this.routeParams.pipe(
            map(params => params.has('clear') && params.get('clear')),
            filter(f => !!f))
            .subscribe(next => {
                this.currentSystemId.next(undefined);
                this.clearBootstrap();
            }));

        this.subscription('system_list', this.service.Systems
            .list()
            .pipe(distinctUntilChanged(),
                withLatestFrom(this.isLoading))
            .subscribe(([systems, isLoading]) => {
                this.systemsList.next(systems || []);
                this.useManualInput.next(!isLoading && systems && systems.length <= 0);
                if (systems) {
                    this.isLoading.next(false);
                }
            }));

        this.subscription('Spaces.initialized', this.service.Spaces.initialised.pipe(first(_ => _)).subscribe(() => {
            this.checkBootstrapped();
        }));
    }

    public toggleManualInput() {
        this.useManualInput.next(!this.useManualInput.value);
    }

    /**
     * Setup the default system for the application to bind to
     */
    public bootstrap() {
        this.currentSystemId
            .pipe(take(1),
                filter(systemId => !!systemId))
            .subscribe((currentSystem) => {
                this.configure(currentSystem);
            });
    }

    public selectItem(engine: EngineSystem) {
        this.currentSystemId.next(engine.id);
    }

    /**
     * Check if the application has previously been bootstrapped
     */
    private checkBootstrapped(): void {
        if (localStorage) {
            const storedSystemId = localStorage.getItem('ACA.PANEL.system') || localStorage.getItem('ACA.CONTROL.system');
            if (storedSystemId) {
                this.currentSystemId.next(storedSystemId);
                return;
            }
        }
    }

    /**
     * Save the bootstrapped ID and redirect to the panel for that ID
     * @param system_id System to bootstrap
     */
    private configure(system_id: string): void {
        if (localStorage) {
            localStorage.setItem('ACA.PANEL.system', system_id);
        }
        this.service.navigate(['panel', system_id]);
    }

    /**
     * Remove any previously set bootstrapping details
     */
    private clearBootstrap() {
        if (localStorage) {
            localStorage.removeItem('ACA.PANEL.system');
        }
    }

}

