import { Component, OnInit } from '@angular/core';
import { BaseComponent } from '../../shared/base.component';
import { catchError, distinctUntilChanged, filter, mergeMap } from 'rxjs/operators';
import { ApplicationService } from '../../services/app.service';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { from, of } from 'rxjs';
import { Space } from '../../services/data/spaces/space.class';

@Component({
    selector: 'pre-load-component',
    template: `
        <div class="bootstrap">
            <div class="options">
                <div class="header fs-big" style="background-color: white">
                    <div class="body">
                        <div class="info-block">
                            <div class="icon">
                                <a-spinner circle-ring color="#000"></a-spinner>
                            </div>
                            <div class="text">
                                Loading system data...
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        </div>
    `,
    styleUrls: ['../bootstrap/bootstrap.component.scss']
})
export class PreloadComponent extends BaseComponent implements OnInit {
    constructor(private service: ApplicationService, private route: ActivatedRoute) {
        super();
    }

    ngOnInit(): void {
        this.subscription('User.currentUser', this.service.Users.currentUser
            .pipe(filter(current => !!current),
                distinctUntilChanged((prev, current) => prev.email === current.email),
                mergeMap(current =>
                    from(this.service.Spaces.show(current.email))
                        .pipe(
                            catchError(_ => of(undefined)))),
                distinctUntilChanged()
            )
            .subscribe((space: Space | undefined) => {
                if (space) {
                    // once we have space, then go to next
                    this.pushToPanel(space.id);
                } else {
                    this.subscription('route.query', this.route.queryParamMap
                        .pipe(distinctUntilChanged())
                        .subscribe((params) => {
                            // query params, check for legacy routes and forward over
                            if (params.keys.length > 0 && (params.has('sys_id')
                                || params.has('system_id')
                                || params.has('clear'))) {
                                this.pushToRoomSelect(params);
                            } else {
                                this.pushIfBootstrapped();
                            }
                        }));
                }
            }));
    }

    private pushToPanel(id: string) {
        this.service.navigate(['panel', id]);
    }

    private pushToRoomSelect(params?: ParamMap) {
        this.service.navigate('bootstrap/room-select', (params as any)?.params);
    }

    private storedSystemId(): string | undefined {
        if (localStorage) {
            return localStorage.getItem('ACA.PANEL.system') || localStorage.getItem('ACA.CONTROL.system');
        }
        return undefined;
    }

    /**
     * Check if the application has previously been bootstrapped
     */
    private pushIfBootstrapped(): void {
        const storedSystemId = this.storedSystemId();
        if (storedSystemId) {
            this.pushToPanel(storedSystemId);
        } else {
            this.pushToRoomSelect();
        }
    }
}
